import React from 'react';

interface Props {
    label: string;
    style: 'switch' | 'button';
    checked: boolean;
    changeValue: (value: boolean) => void;
    disabled: boolean;
}

export default class Checkbox extends React.Component<Props> {
    public static defaultProps = {
        style: 'switch',
        disabled: false
    };

    private click(e: React.MouseEvent) {
        e.stopPropagation();
        this.props.changeValue(!this.props.checked);
    }

    public render() {
        try {
            let style = 'checkbox ' + this.props.style;
            if (this.props.checked) {
                style += ' checked';
            }
            if (this.props.disabled) {
                style += ' disabled';
            }

            let toggle = null;
            if (this.props.style === 'switch') {
                toggle = (
                    <div className='toggle-container'>
                        <div className='toggle'/>
                    </div>
                );
            }

            return (
                <div className={style} onClick={e => this.click(e)}>
                    <div className='checkbox-label'>{this.props.label}</div>
                    {toggle}
                </div>
            );

        } catch (ex) {
            console.error(ex);
            return null;
        }
    }
}
